<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/notice')">공지</li>
			<li @click="goto('/admin/news')" class="current">뉴스</li>
		</ul>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								id="select-all"
								v-model="selectAll"
							/>
							<label for="select-all"></label>
						</div>
						<div class="prop index" style="width: 100px;">No.</div>
						<div class="prop" style="width: 625px;">제목</div>
						<div class="prop">게시일자</div>
						<div class="prop">수정</div>
					</li>
					<slot v-if="!newsList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in newsList" :key="item.id" class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop index" style="width: 100px;">{{ item.id }}</div>
							<div class="prop justify-flex-start" style="width: 625px;">
								{{ item.title }}
							</div>
							<div class="prop">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
							<div class="prop">
								<router-link
									:to="{ name: 'EditNews', params: { id: item.id } }"
								>
									<Edit />
								</router-link>
							</div>
						</li>
					</slot>
				</ul>
				<div class="bottom-container">
					<Pagination
						v-if="totalPage !== -1"
						:listRowCt="15"
						:pageLinkCt="5"
						:totalCount="totalPage"
						:url="`/admin/news`"
					/>
					<div class="btn-container">
						<div class="checked-info body3 marR-30">
							<span class="number">{{ selectedCnt }}</span
							><span>개 선택됨</span>
						</div>
						<button
							class="solid-btn red"
							@click="isDeleteModalViewed = true"
							:disabled="selected.length < 1"
						>
							선택 글 삭제
						</button>
						<router-link :to="{ name: 'AddNews' }">
							<button class="solid-btn green">
								새 글 등록
							</button>
						</router-link>
					</div>
				</div>
				<ModalView
					v-if="isDeleteModalViewed"
					@close-modal="isDeleteModalViewed = false"
				>
					<DeleteItem
						@close-modal="isDeleteModalViewed = false"
						@confirm="deleteNews"
						:count="selected.length"
					></DeleteItem>
				</ModalView>
			</div>
		</div>
	</div>
</template>
<script>
import { fetchNews, deleteNews, fetchNewsTotalCount } from '@/api/news';
import Pagination from '@/components/common/Pagination';
import ModalView from '@/components/common/ModalView';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import Edit from '@/assets/icon/Edit';

export default {
	components: { DeleteItem, Edit, ModalView, Pagination },
	created() {
		this.currentPage = this.$route.params.page;

		this.fetchNews(this.currentPage);
		this.fetchNewsTotalCount();
	},
	data() {
		return {
			newsList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
			isDeleteModalViewed: false,
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.newsList
					? this.selected.length == this.newsList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.newsList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchNews(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchNews(page - 1);
				console.log(page);
				this.newsList = data;
			} catch (e) {
				alert(e);
			}
		},
		async fetchNewsTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchNewsTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
		async deleteNews() {
			this.isLoading = true;
			try {
				await deleteNews(this.selected);
				this.fetchNews(this.currentPage);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchNews(this.currentPage);
			}
		},
	},
};
</script>
<style></style>
